.customizers > .toggle-button {
    display: none;
    outline: none;
}

.customizers > .close-button {
    display: block;
    
    width: 150px;
    margin: 30px auto 20px;
    
    padding: 12px;
    border: none;
    border-radius: 45px;
    background-color: var(--purple);
    color: white;
    font-size: 15px;
    
    cursor: pointer;
    outline: none;
}
.customizers > .close-button:hover {
    background-color: #2B039B;
}
.customizers > .close-button.disabled {
    opacity: 0.4;
    cursor: auto;
}

.customizers > div:not(:nth-child(2)) {
    margin-top: 20px;
}
.customizers > div > button {
    display: block;
    width: 100%;
    margin: 0;
    
    position: relative;
    border: none;
    padding: 10px 0;
    
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 0.4px;
    text-align: left;
    
    cursor: pointer;
    background-color: transparent;
    outline: none;
}
.customizers > div > button.close-disabled {
    cursor: auto;
}
.customizers > div > button.disabled span {
    opacity: 0.4;
}
.customizers > div > button> svg {
    width: 20px;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(0, -50%);
}
.customizers > div > button.close-disabled svg {
    opacity: 0.4;
}
.customizers > div.collapsed > button > svg {
    transform: scale(1, -1) translate(0, 50%);
}
.customizers > div > div{
    margin: 10px 0;
}

@media screen and (max-width: 600px) {
    .customizers.closed > :not(:first-child) {
        display: none;
    }
    .customizers.closed > .toggle-button {
        border-bottom: none;
    }
    .customizers.closed > .toggle-button svg {
        transform: scale(1, -1) translate(0, 50%);
    }
    .customizers > .toggle-button {
        display: block;
        position: relative;
        
        margin: 0;
        width: 100%;
        
        padding: 14px 15px 9px;
        border: none;
        border-bottom: 1px solid var(--light-grey);
        background-color: white;
        
        text-align: left;
        font-size: 17px;
        
        cursor: pointer;
    }
    .customizers > .toggle-button.disabled {
        cursor: auto;
    }
    .customizers > .toggle-button svg {
        position: absolute;
        right: 15px;
        top: 50%;
        transform: translate(0, -50%);
    }
    .customizers > .toggle-button.disabled svg {
        opacity: 0.4;
    }
    .customizers > .close-button {
        margin-top: 20px;
    }
    .customizers {
        background-color: white;
        border-radius: 3px;
        padding: 0 9px;
    }
    .customizers > div {
        margin: 15px;
    }
    .customizers > div:nth-child(2) {
        margin-top: 18px;
    }
}
