.usages button {
    display: flex;
    width: 100%;
    margin: 0;
    align-items: center;
    font-size: 15px;
    
    border-radius: 5px;
    padding: 9px;
    cursor: pointer;
    
    outline: none;
}
.usages button:hover:not(.grey-background) {
    filter: brightness(0.9);
}
.usages button.grey-background:not(:hover) {
    filter: grayscale(100%) brightness(1.4);
}

.usages button:not(:first-child) {
    margin-top: 9px;
}
.usages svg {
    width: 50px;
    height: 35px;
}
.usages p {
    margin: 0 0 0 10px;
    flex: 1;
    text-align: left;
}
