.product .specifications .title {
    margin: 0;
    
    font-size: 20px;
    font-weight: 500;
}
.product .specifications .specification {
    margin-top: 15px;
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: 5px;
}
.product .specifications .specification p {
    margin: 0;
}
.product .specifications .specification .name {
    color: var(--slightly-lighter-grey);
}
.product .specifications .specification .value {
    text-align: right;
}
