.product {
    min-height: calc(100vh - var(--header-height));

    display: grid;
    grid-template-columns: 1fr minmax(0, 1060px) 250px 1fr;
    grid-column-gap: 10px;
}

.product .content {
    grid-column: 2 / 3;
    position: relative;
    
    display: grid;
    grid-template-columns: auto 300px;
    grid-gap: 100px;
    
    padding: 80px 45px;
    background-color: white;
}

.product .content.mobile {
    display: block;
    padding: 0 30px 30px;
}
.product .full-height {
    display: grid;
    align-content: space-around;
    
    padding: 30px 0;
}

.product .advertisements {
    grid-column: 3 / 4;
    margin-top: 10px;
    
    height: 600px;
    background-color: white; /* Placeholder color */

    opacity: 0.5; /* Temporary transparency */
}

.product .preview,
.product .info,
.product .specifications {
    align-self: center;
}

.product .page-selector {
    position: absolute;
    top: 0;
    right: 0;
}

@media screen and (max-width: 1100px) {
    .product .content {
        grid-gap: 30px;
        padding: 80px 30px;
    }
}

@media screen and (max-width: 1000px) {
    .product {
        grid-template-columns: 0 1fr 0;
    }
    .product .content {
        padding: 80px 35px;
    }
    .product .advertisements {
        display: none;
    }
}
@media screen and (max-width: 700px) {
    .product .content {
        grid-gap: 10px;
    }
}
