.range-container {
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-template-rows: auto auto;
    grid-row-gap: 5px;
}
.range-container.disabled {
    opacity: 0.4;
}

.range-container > input {
    margin: 0;
    width: 55px;
    text-align: center;
    border: 1px solid var(--slightly-lighter-grey);
    padding: 5px;
    border-radius: 3px;
    font-size: 11px;
    font-family: inherit;
    background-color: white;
    color: black;
}

.range-container .slider-container {
    grid-column: 1 / 4;
    height: 20px;
    touch-action: none;
    user-select: none;
}

.thumb {
    width: 21px;
    height: 21px;

    border-radius: 50%;
    border: 1px solid var(--slightly-lighter-grey);

    background-color: white;
}
.range-container:not(.disabled) .thumb {
    cursor: pointer;
    outline: none;
}

.range-container:not(.disabled) .thumb:hover {
    border-color: var(--semi-dark-grey);
    background-color: var(--light-grey);
}

.track {
    margin-top: 6px;

    height: 9px;
    border-radius: 6.5px;
    background-color: var(--light-grey);
}
.track-1 {
    background-color: var(--purple);
}