.products {
    display: grid;
}

.products > a, .products > div {
    display: grid;
    grid-gap: 10px;
    background-color: white;
    border-radius: 3px;
    text-decoration: none;
    color: inherit;
    transition: background-color 0.1s, filter 0.1s, opacity 0.1s;
}
.products > a:hover {
    background-color: #E3E3E3;
}
.products .product-image {
    transition: filter 0.1s;
}
.products > a:hover .product-image {
    filter: brightness(89%);
}
.products .message {
    grid-column: 1 / -1;
}
.products .message p {
    margin: 80px 0;
    
    font-size: 18px;
    text-align: center;
    padding: 10px;
    line-height: 1.5em;
}
.products p {
    margin: 0;
}
.products .product-price {
    color: var(--purple);
}
.products > p {
    grid-column: 1 / -1;
    text-align: center;
    font-size: 16px;
    color: var(--grey);
    margin: 20px 0;
}

.products.layout-grid {
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: inherit;
}
.products.layout-grid > a {
    padding: 25px;
    grid-template-columns: 1fr auto;
    grid-template-rows: 1fr auto auto;
}
.products.layout-grid .header {
    display: none;
}
.products.layout-grid a p {
    font-size: 14px;
}
.products.layout-grid .product-image {
    grid-area: 1 / 1 / 2 / 3;
    width: 100%;
    align-self: center;
}
.products.layout-grid .product-title {
    grid-area: 2 / 1 / 3 / 3;
}
.products.layout-grid .product-price {
    grid-area: 3 / 1 / 3 / 2;
}
.products.layout-grid .product-extra {
    grid-area: 3 / 2 / 3 / 3;
    text-align: right;
}
.products.layout-grid > .top-choice {
    grid-column: 1 / -1;
    position: relative;
    
    padding: 65px 20px;
    grid-template-columns: 250px auto;
    grid-template-rows: 1fr auto auto auto 1fr;
    grid-gap: 10px 30px;
    align-items: center;
    
}
.products.layout-grid > .top-choice::before {
    position: absolute;
    left: 5px;
    top: 5px;
    
    padding: 8px;
    
    background-color: var(--purple);
    color: white;
    content: "Rekommenderad";
    font-size: 15px;
    border-radius: 3px;
}
.products.layout-grid > .top-choice .product-image {
    grid-area: 1 / 1 / 6 / 2;
}
.products.layout-grid > .top-choice .product-title {
    font-size: 20px;
    grid-area: 2 / 2 / 3 / 3
}
.products.layout-grid > .top-choice .product-price {
    font-size: 18px;
    grid-area: 3 / 2 / 4 / 3;
}
.products.layout-grid > .top-choice .product-extra {
    font-size: 18px;
    grid-area: 4 / 2 / 5 / 3;
    text-align: left;
}

.products.layout-list {
    grid-template-columns: 1fr;
    grid-gap: 10px;
}
.products.layout-list > a {
    padding: 10px;
    grid-template-columns: 65px 1fr 65px 45px;
    align-items: center;
}
.products.layout-list .header {
    display: grid;
    grid-template-columns: 1fr 65px 45px;
    padding: 10px 10px 10px 90px;
}
.products.layout-list .header p {
    color: var(--grey);
    font-size: 13px;
}
.products.layout-list a p {
    font-size: 15px;
}
.products.layout-list .product-image {
    grid-area: 1 / 1 / 2 / 2;
    width: 100%;
}
.products.layout-list > .top-choice .product-title::before {
    color: var(--purple);
    content: "Rekommenderad\000A";
    white-space: pre;
    font-size: 13px;
}

@media screen and (max-width: 1050px) {
    .products.layout-grid {
        grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    }
    .products.layout-grid > a {
        padding: 20px;
    }
    .products.layout-grid > .top-choice {
        grid-template-columns: 155px auto;
    }
    .products.layout-grid > .top-choice .product-title {
        font-size: 18px;
    }
    .products.layout-grid > .top-choice .product-price {
        font-size: 15px;
    }
    .products.layout-grid > .top-choice .product-extra {
        font-size: 15px;
    }
}

@media screen and (max-width: 600px) {
    .products.hidden {
        display: none;
    }
    .products.layout-grid {
        grid-template-columns: repeat(auto-fit, minmax(145px, 1fr));
    }
    .products.layout-list .header {
        display: none;
    }
    .products.layout-list > a {
        grid-template-columns: 60px 1fr 60px;
    }
    .products.layout-list .product-extra {
        display: none;
    }
}

.products.loading > a, .products.loading > div {
    filter: grayscale(1);
    opacity: 0.7;
}
