@import "../variables.css";

.feedback-area {
    position: relative;
    height: 105px;
}
.feedback-area textarea {
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    margin: 0;
    
    border: none;
    padding: .5em;
    
    border-radius: 3px 3px 10px 3px;
    resize: none;

    font-family: inherit;
    font-size: 15px;

    opacity: 0.9;
}
.feedback-area button {
    position: absolute;
    right: 0;
    bottom: 0;

    margin: 0;
    padding: .3em 10px;
    border: none;
    border-bottom-right-radius: 3px;
    font-size: 15px;
    font-weight: 500;
    
    background: var(--purple);
    color: white;
    cursor: pointer;
    outline: none;
}
.feedback-area button:hover {
    background: rgb(35, 0, 150);
}
.feedback-area button:active {
    background: rgb(20, 0, 120);
}
.feedback-area p {
    position: absolute;
    left: 0;
    bottom: 0;
    margin: 0;
    padding: 9px 12px;
    color: var(--purple);
    
    transition: visibility 100ms, opacity 100ms;
    visibility: hidden;
    opacity: 0;
}
.feedback-area p.visible {
    visibility: visible;
    opacity: 1;
}
