@import "../../variables.css";

footer {
    padding: 23px 0;
    height: 180px;
    background-color: var(--black);
}
footer .container {
    margin: 0 auto;
    
    max-width: 1200px;
    height: 100%;

    display: grid;
    justify-content: space-evenly;
    justify-items: center;
    grid-template-columns: repeat(7, auto);
    grid-template-rows: 1fr auto;
}
footer h5 {
    font-size: 16px;
    font-weight: 500;
    color: var(--light-grey);;
    margin: 0;
    margin-bottom: 10px;
}
footer .separator {
    grid-row: 1 / 3;
    border-right: 1px solid var(--semi-dark-grey);
}
footer .feedback {
    grid-row: 1 / 3;
    width: 200px;
}
footer .info {
    grid-row: 1 / 3;
}
footer .info a {
    display: block;
    margin-top: 7px;

    font-size: 14px;

    color: var(--semi-light-grey);
    text-decoration: none;
}
footer .social-media {
    display: grid;
    grid-template-columns: 33px 33px;
    grid-template-rows: 33px 33px;
    grid-gap: 12px;
    
    width: 78px;
    height: 78px;
    align-self: center;
}
footer .social-media svg {
    color: var(--bg-grey);
    width: 33px;
    height: 33px;
}
footer .copyright {
    text-align: center;
    color: var(--grey);
    width: 110px;
    margin: 0;
    font-size: 13px;
    line-height: 18px
}

footer a:hover {
    opacity: 0.7;
}

/* Narrow layout */
@media screen and (max-width: 720px) {
    footer {
        height: initial;
        padding: 23px;
    }
    footer .container {
        justify-content: space-between;
        grid-template-columns: auto auto;
        grid-template-rows: auto auto auto auto;
        grid-row-gap: 23px;
    }
    footer .separator {
        display: none
    }
    footer .feedback {
        grid-area: 2 / 1 / 3 / 4;
        width: 100%;
    }
    footer .info {
        grid-row: 1 / 2;
    }
    footer .social-media {
        grid-template-columns: 33px 33px 33px 33px;
        grid-template-rows: 33px;
        grid-area: 3 / 1 / 4 / 4;
        margin-top: 25px;

        width: 168px;
        height: 33px;
    }
    footer .copyright {
        grid-area: 4 / 1 / 5 / 4;
        width: 100%;
    }
}
