.preview .carousel {
    position: relative;
}
.preview .carousel__inner-slide {
    display: grid;
    grid-template-rows: 1fr auto;
    grid-row-gap: 20px;
}
.preview .preview-image {
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}
.preview .preview-image-source {
    margin: 0;
    color: var(--slightly-lighter-grey);
    font-size: 13px;
    text-align: center;
}
.preview .thumbnails {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.preview .thumbnail {
    width: 80px;
    height: 60px;
    
    border: none;
    border-radius: 3px; 
    padding: 5px;
    background-color: white;
    
    cursor: pointer;
    
    transition: filter 0.2s;
}
.preview .thumbnail:hover {
    filter: brightness(0.9);
}
.preview .thumbnail img {
    max-width: 100%;
    max-height: 100%;
}
.preview .dots {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
}
.preview .dots button {
    display: block;
    width: 30px;
    height: 30px;
    padding: 0;
    border: none;
    background: transparent;
}
.preview .dots span {
    margin: 0 10px;
    
    display: block;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    
    border: none;
    padding: 0;
    background-color: var(--light-grey);
}
.preview .dots .carousel__dot--selected span {
    background-color: var(--grey);
}
