.menu {
    position: absolute;
    right: 0;
    top: 0;
    height: calc(100%);
    width: 270px;
    z-index: 1;
    
    background-color: #181818;
    overflow-x: hidden;
    
    transition: width 0.3s ease-out;
}
.menu.hidden {
    width: 0;
}
.menu.container {
    width: 270px;
}

.menu .close-icon {
    display: block;
    box-sizing: content-box;
    width: 20px;
    color: white;
    cursor: pointer;
    padding: 10px;
    margin: 5px;
}
.menu h5 {
    margin: 15px 39px;
    color: var(--slightly-lighter-grey);
    font-size: 20px;
    font-weight: 500;
}
.menu a {
    margin: 10px 40px;
    color: white;
    display: block;
    font-size: 15px;
    text-decoration: none;
}

.menu .close-icon:hover,
.menu a:hover {
    opacity: 0.7;
}
