.checkbox-container {
    display: flex;
    margin-bottom: 18px;
    vertical-align: middle;
    line-height: 24px;
    cursor: pointer;
}
.checkbox-container.disabled {
    opacity: 0.4;
    cursor: default;
}

.checkbox-container .checkbox {
    width: 24px;
    height: 24px;
    background-color: white;
    border: 1px solid var(--slightly-lighter-grey);
    border-radius: 3px;
    position: relative;
    text-align: center;
}
.checkbox-container:not(.disabled) .checkbox:hover {
    background-color: var(--light-grey);
}
.checkbox-container p {
    margin: 0;
    padding-left: 9px;
    display: block;
    flex: 1;
    user-select: none;
}
.checkbox-container.enabled p:hover {
    opacity: 0.6;
}
