@import "../../variables.css";

.product-header {
    display: grid;
    grid-template-columns: 1fr minmax(0, 1250px) 1fr;
    grid-gap: inherit;
    
    background-color: #181818;
    color: white;
}
.product-header .container {
    grid-column: 2 / 3;
    
    width: 100%;
    margin: 0 auto;
    
    padding: 24px 0;
    display: grid;
    grid-gap: inherit;
    
    grid-template-rows: auto 20px;
    grid-template-columns: 1fr auto 250px;
    
    align-items: center;
}

.product-header .info {
    grid-area: 1 / 1 / 3 / 2;
    padding: 30px 0;
    
    display: grid;
    grid-template-columns: 150px 1fr;
    grid-template-rows: 40px 1fr;
    grid-gap: 13px 20px;
    align-items: center;
}
.product-header .info .icon {
    grid-area: 1 / 1 / 3 / 2;
    width: 100%;
}
.product-header .info h1 {
    grid-area: 1 / 2 / 2 / 3;
    margin: 0 0 0 -0.05em;
    font-size: 35px;
    font-family: var(--header-font);
    font-weight: 600;
}
.product-header .info p {
    margin: 0;
    max-width: 400px;
    letter-spacing: .5px;
    font-size: 15px;
    line-height: 1.3em;

    color: var(--semi-light-grey);
}
.product-header .layout-selector {
    grid-area: 2 / 2 / 3 / 3;
}

@media screen and (max-width: 900px) {
    .product-header .container {
        grid-template-columns: 1fr auto;
    }
}

@media screen and (max-width: 600px) {
    .product-header {
        height: initial;
    }
    .product-header .layout-selector {
        grid-area: 2 / 2 / 3 / 3;
    }
    .product-header .info {
        grid-area: 1 / 1 / 2 / 3;
        grid-template-columns: 63px 1fr;
        grid-template-rows: 35px 1fr;
        grid-gap: 10px;
        padding: 0;
    }
    .product-header .info .icon {
        grid-area: 1 / 1 / 2 / 2;
    }
    .product-header .info h1 {
        font-size: 22px;
        font-weight: 600;
    }
    .product-header .info p {
        grid-area: 2 / 1 / 3 / 3;
    }
}
