.product .info .title {
    margin-bottom: 25px;
}
.product .info .price {
    margin: 0 0 5px 0;
    
    font-size: 22px;
    font-weight: 500;
    color: var(--purple);
}

.product .info .dropdown {
    display: block;
    position: relative;
    width: 150px;
}
.product .info .dropdown button {
    display: block;
    margin: 0;
    padding: 5px 0;
    border: none;
    width: 100%;
    cursor: pointer;
    background: none;
    text-align: left;
    font-size: 14px;
    outline: none;
}
.product .info .dropdown > button {
    position: relative;
    
    color: var(--grey);
}
.product .info .dropdown > button svg {
    position: absolute;
    right: 2px;
    top: 10px;
}
.product .info .dropdown div {
    position: absolute;
    left: 0;
    top: 100%;
    width: 100%;
    background-color: var(--bg-grey);
    z-index: 1;
}
.product .info .dropdown div button {
    display: flex;
    padding: 5px;
}
.product .info .dropdown button:hover {
    background: #0001;
}
.product .info .dropdown .website-name {
    flex: 1;
    color: var(--black);
}
.product .info .dropdown .website-price {
    color: var(--grey);
}

.product .info .go-to {
    display: inline-block;
    width: 150px;
    margin-top: 25px;
    
    padding: 10px;
    background-color: var(--purple);
    border-radius: 5px;
    
    font-size: 12px;
    text-decoration: none;
    text-align: center;
    color: white;
}
.product .info .go-to:hover {
    opacity: 0.8;
}

.product .info.mobile .price {
    font-size: 28px;
}
.product .info.mobile {
    text-align: center;
}
.product .info.mobile .dropdown {
    margin: 0 auto;
    width: 180px;
}
.product .info.mobile .dropdown > button {
    text-align: center;
    padding: 10px;
}
.product .info.mobile .dropdown > button svg {
    position: static;
    margin-left: 10px;
}
.product .info.mobile .dropdown button {
    font-size: 15px;
}
.product .info.mobile .dropdown div button {
    padding: 10px;
}
.product .info.mobile .go-to {
    width: 200px;
    font-size: 16px;
}
