.priority-container {
    display: flex;
    margin-bottom: 18px;
    vertical-align: middle;
    line-height: 24px;
    cursor: pointer;
}
.priority-container.disabled {
    cursor: default;
    opacity: 0.4;
}

.priority-container input {
    display: none;
}
.priority-container .checkbox {
    width: 24px;
    height: 24px;
    background-color: white;
    border: 1px solid var(--grey);
    border-radius: 3px;
    position: relative;
    text-align: center;
}
.priority-container:not(.disabled) .checkbox:hover {
    background-color: var(--light-grey);
}
.priority-container p {
    margin: 0;
    padding-left: 9px;
    display: block;
    flex: 1;
    user-select: none;
}
.priority-container:not(.disabled) p:hover {
    opacity: 0.6;
}

.priority-container .dropdown {
    position: relative;
    width: 110px;
}
.priority-container .dropdown button {
    display: block;
    margin: 0;
    padding: 0;
    border: none;
    width: 100%;
    height: 24px;
    cursor: pointer;
    background: none;
    text-align: left;
    font-size: 12px;
    outline: none;
}
.priority-container .dropdown > button {
    position: relative;
    border-bottom: 1px solid var(--grey);
}
.priority-container .dropdown > button svg {
    position: absolute;
    right: 2px;
    top: 8px;
}
.priority-container .dropdown div {
    position: absolute;
    left: 0;
    top: 100%;
    width: 100%;
    background-color: var(--light-grey);
    z-index: 1;
}
.priority-container .dropdown div button {
    padding: 0 5px;
}
.priority-container .dropdown button:hover {
    background: #0001;
}
