.document {
    padding: 20px;
    min-height: 100vh;

    background-color: white;
}

@media only screen and (min-width: 1000px) {
    .document {
        margin: auto;
    
        padding: 40px;
        width: 750px;
        min-height: 900px;
    }
}

.document h3 {
    margin: 0 0 16px 0;

    font-size: 29px;
}

.document h4 {
    margin: 30px 0 4px 0 ;
}

.document p {
    margin: 0 0 6px 0;

    font-size: 15px;

    color: var(--dark-grey);
    text-align: justify;
}
.document p.large {
    margin: 40px 0 20px 0;

    font-size: 22px;
    font-family: var(--header-font);
    text-align: left;
}

.document em {
    color: black;
}

.document li {
    font-size: 15px;

    color: darkslategrey;
}

@media only screen and (min-width: 1000px) {
    .document p {
        font-size: auto;
        
        text-align: auto;
    }
}
