.customization {
    min-height: 100vh;
    
    display: grid;
    grid-template-columns: 1fr 250px minmax(0, 710px) 250px 1fr;
    grid-template-rows: auto auto 1fr;
    grid-gap: 20px;
    align-items: start;
}

.customization .product-header {
    grid-column: 1 / -1;
}
.customization .customizers {
    grid-column: 2 / 3;
}
.customization .products {
    grid-column: 3 / 4;
}
.customization .advertisements {
    grid-column: 4 / 5;
    
    height: 600px;
    background-color: white; /* Placeholder color */

    opacity: 0.5; /* Temporary transparency */
}

@media screen and (max-width: 1100px) {
    .customization {
        grid-gap: 20px;
    }
}
@media screen and (max-width: 900px) {
    .customization {
        grid-template-columns: 0 250px 1fr 0;
        grid-gap: 15px;
    }
    .customization .advertisements {
        display: none;
    }
}

@media screen and (max-width: 600px) {
    .customization {
        grid-template-columns: 0 1fr 0;
        grid-template-rows: auto auto auto 1fr;
        grid-gap: 10px;
    }
    .customization .products {
        grid-column: 2 / 3;
    }
    .customization .header {
        grid-template-columns: 0 1fr 0;
    }
    .customization .header .icon {
        display: none;
    }
}

.customization .popup {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #0008;
    z-index: 10;
}
.customization .popup .content {
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 450px;
    max-width: calc(100% - 30px);
    
    background: var(--bg-grey);
    padding: 20px;
    border-radius: 5px;
}
.customization .popup .close-icon {
    position: absolute;
    right: 5px;
    top: 5px;
    box-sizing: content-box;
    width: 20px;
    height: 20px;
    padding: 5px;
    cursor: pointer;
}
.customization .popup h4 {
    margin: 0;
    font-size: 18px;
}
.customization .popup .feedback-area {
    height: 130px;
}
