:root {
    /* Screen sizes */
    --tablet: 600px;
    --laptop: 1000px;
    --desktop: 1500px;

    /* Colors */
    --purple: rgb(49, 0, 187);
    --light-purple: rgb(170, 146, 238);

    /* Greyscale */
    --black: rgb(13, 13, 13);
    --dark-grey: rgb(17, 17, 17);
    --semi-dark-grey: rgb(46, 46, 46);
    --grey: rgb(77, 77, 77);
    --slightly-lighter-grey: rgb(94, 94, 94);
    --semi-light-grey: rgb(187, 187, 187);
    --light-grey: rgb(216, 216, 216);
    --bg-grey: rgb(240, 240, 240);

    /* Fonts */
    --main-font: "Roboto", sans-serif;
    --header-font: "Montserrat", sans-serif;
    
    /* UI element sizes */
    --header-height: 50px;
}
