@import "../variables.css";

.main-page .header {
    max-width: 1300px;
    margin: 0 auto;
}
.main-page .full-height {
    display: grid;
    grid-template-rows: 1fr auto;
}
.main-page .header {
    width: 100%;
    position: relative;
    overflow: hidden;

    display: grid;
    grid-template-rows: 1fr auto 2fr;
    padding: 25px;
}
.main-page .header .background {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 60%;
    z-index: -1;
}
.main-page .header .text {
    grid-row: 2;
}
.main-page .header .text svg {
    width: 160px;
}
.main-page .header .text h1 {
    font-family: var(--header-font);
    font-weight: 700;
    font-size: 58px;
    margin: 10px 0 20px -0.07em;
}
.main-page .header .text p {
    font-size: 18px;
    margin: 0;
    max-width: 500px;
    line-height: 33px;
    color: var(--grey);
}

.main-page .categories {
    display: flex;
    background-color: var(--black);
    justify-content: center;
    align-items: center;
}

.main-page .category {
    padding: 20px;
    display: grid;
    grid-template-rows: 80px auto;
    grid-gap: 20px;
    text-decoration: none;
    width: 150px;
}
.main-page .category-image {
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: 50% 100%;
}
.main-page .category-name {
    margin: 0;
    text-align: center;
    color: white;
    font-size: 16px;
    font-weight: normal;
}
.main-page .more-is-coming {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 150px;
}
.main-page .more-is-coming p {
    color: var(--grey);

    text-align: center;
}
.main-page .category-separator {
    border-left: 1px solid var(--semi-dark-grey);
    height: 120px;
}

/* Shrink the category icons when the screen is really small */
@media screen and (max-width: 450px) {
    .main-page .category, .main-page .more-is-coming {
        width: calc((100% - 1px) / 3);
    }
    
    .main-page .category-separator {
        height: 80px;
    }
}

@media screen and (max-width: 800px) {
    .main-page .header .text svg {
        width: 160px;
    }
    .main-page .header .text h1 {
        font-size: 56px;
    }
    .main-page .header .text p {
        font-size: 20px;
        margin: 8px 0 18px 0;
    }
}
@media screen and (max-width: 650px) {
    .main-page .header .text svg {
        width: 140px;
    }
    .main-page .header .text h1 {
        font-size: 44px;
        margin-top: 5px;
        margin-bottom: 15px;
    }
    .main-page .header .text p {
        font-size: 18px;
    }
}

@media screen and (max-width: 500px) {
    .main-page .header .background {
        right: 0;
        bottom: 0;
        width: 100%;
        transform: translate(0, 50%);
    }
    .main-page .header .text {
        text-align: center;
        max-width: 290px;
        margin: 0 auto;
    }
    .main-page .header .text svg {
        display: none;
    }
    .main-page .header .text h1 {
        font-size: 46px;
        margin-top: 0;
    }
    .main-page .header .text p {
        font-size: 17px;
    }
}

@media screen and (min-width: 1300px) {
    .main-page .header .background {
        right: 25px;
    }
}
